@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:400,500,600,700|Source+Sans+Pro:400,600,700&display=swap);

body{background:#ddeefc;font-family:'Source Sans Pro', sans-serif;font-size:16px}*{box-sizing:border-box}*:focus{outline:none}.wrapper{min-height:100vh;display:flex;padding:50px 15px}@media screen and (max-width: 700px), (max-height: 500px){.wrapper{flex-wrap:wrap;flex-direction:column}}.card-form{max-width:570px;margin:auto;width:100%}@media screen and (max-width: 576px){.card-form{margin:0 auto}}.card-form__inner{background:#fff;box-shadow:0 30px 60px 0 rgba(90,116,148,0.4);border-radius:10px;padding:35px;padding-top:180px}@media screen and (max-width: 480px){.card-form__inner{padding:25px;padding-top:165px}}@media screen and (max-width: 360px){.card-form__inner{padding:15px;padding-top:165px}}.card-form__row{display:flex;align-items:flex-start}@media screen and (max-width: 480px){.card-form__row{flex-wrap:wrap}}.card-form__col{flex:auto;margin-right:35px}.card-form__col:last-child{margin-right:0}@media screen and (max-width: 480px){.card-form__col{margin-right:0;flex:unset;width:100%;margin-bottom:20px}.card-form__col:last-child{margin-bottom:0}}.card-form__col.-cvv{max-width:150px}@media screen and (max-width: 480px){.card-form__col.-cvv{max-width:none;max-width:initial}}.card-form__group{display:flex;align-items:flex-start;flex-wrap:wrap}.card-form__group .card-input__input{flex:1 1;margin-right:15px}.card-form__group .card-input__input:last-child{margin-bottom:0px;margin-right:0}.card-form__button{width:100%;height:55px;background:#2364d2;border:none;border-radius:5px;font-size:22px;font-weight:500;font-family:'Source Sans Pro', sans-serif;box-shadow:3px 10px 20px 0px rgba(35,100,210,0.3);color:#fff;margin-top:20px;cursor:pointer}@media screen and (max-width: 480px){.card-form__button{margin-top:10px}}.card-list{margin-bottom:-130px}@media screen and (max-width: 480px){.card-list{margin-bottom:-120px}}.card-input{margin-bottom:20px}.card-input__label{font-size:14px;margin-bottom:5px;font-weight:500;color:#1a3b5d;width:100%;display:block;-webkit-user-select:none;-ms-user-select:none;user-select:none}.card-input__input{width:100%;height:50px;border-radius:5px;box-shadow:none;border:1px solid #ced6e0;transition:all 0.3s ease-in-out;font-size:18px;padding:5px 15px;background:none;color:#1a3b5d;font-family:'Source Sans Pro', sans-serif}.card-input__input:hover,.card-input__input:focus{border-color:#3d9cff}.card-input__input:focus{box-shadow:0px 10px 20px -13px rgba(32,56,117,0.35)}.card-input__input.-select{-webkit-appearance:none;background-image:url("data:image/png;base64,");background-size:12px;background-position:90% center;background-repeat:no-repeat;padding-right:30px}.github-btn{position:absolute;right:40px;bottom:50px;text-decoration:none;padding:15px 25px;border-radius:4px;box-shadow:0px 4px 30px -6px rgba(36,52,70,0.65);background:#24292e;color:#fff;font-weight:bold;letter-spacing:1px;font-size:16px;text-align:center;transition:all 0.3s ease-in-out}@media screen and (min-width: 500px){.github-btn:hover{transform:scale(1.1);box-shadow:0px 17px 20px -6px rgba(36,52,70,0.36)}}@media screen and (max-width: 700px){.github-btn{position:relative;bottom:auto;right:auto;margin-top:20px}.github-btn:active{transform:scale(1.1);box-shadow:0px 17px 20px -6px rgba(36,52,70,0.36)}}

